import './App.css';
import Toolbar      from './Toolbar.jsx';
import UserView     from './UserView.jsx';
import RepoView     from './RepoView.jsx';

const App = (props, {view, data}, actions)=>
  <body className='App fit fullbleed'>
    <Toolbar title={data} />
    {   view === 'user'  ? <UserView user={data} />
      : view === 'repo'  ? <RepoView repo={data} />
      : view === 'login' ? <LoginView />
      : null }
  </body>;

App.state = {
  onInit: (props, state, {onHashChange})=>{
    window.onhashchange = onHashChange;
    return onHashChange();
  },
  onHashChange: (props, state, {viewRepo, viewUser})=>{
    const hash = window.location.hash;
    if(hash){
      const [, owner, repo] = hash.split('/');
      if(repo)       return viewRepo(`${owner}/${repo}`);
      else if(owner) return viewUser(owner);
    }
    return {view: 'waiting'};
  },
  viewUser: (props, state, actions, user)=>({view: 'user', data: user}),
  viewRepo: (props, state, actions, repo)=>({view: 'repo', data: repo}),
  viewLogin: (props, state, actions)=>({view: 'login'})
};

